import React, { useState, useEffect } from 'react';
import styles from '../../styles/AnimatedSection.module.css';

const AnimatedSection = () => {
  const [showFirst, setShowFirst] = useState(true);

  // Automatically switch between sections every 5 seconds
  useEffect(() => {
    const interval = setInterval(() => {
      setShowFirst((prev) => !prev);
    }, 5000); // Change every 5 seconds

    return () => clearInterval(interval); // Cleanup on unmount
  }, []);

  return (
    <div className={styles.animatedSection}>
      {/* First Section */}
      <div
        className={`${styles.content} ${showFirst ? styles.active : styles.hidden}`}
      >
        <h1>Hey guys <span role="img" aria-label="waving-hand">👋</span>! I am Karim</h1>
        <p>Senior Data Engineer with 6 years of experience and a background in Computer Science.</p>
      </div>

      {/* Second Section */}
      <div
        className={`${styles.content} ${!showFirst ? styles.active : styles.hidden}`}
      >
        <h1>Data enthusiast <span role="img" aria-label="gear">⚙️</span></h1>
        <p>Strong team player who develops and maintains large-scale infrastructure and implements complex data pipelines.</p>
      </div>
    </div>
  );
};

export default AnimatedSection;
