import React from 'react';
import AnimatedSection from './sections/AnimatedSection';
import SkillsSection from './sections/SkillsSection';
import ExperienceSection from './sections/ExperienceSection';
import EducationSection from './sections/EducationSection';
import CertificationsSection from './sections/CertificationsSection';
import LanguagesSection from './sections/LanguagesSection';

import styles from '../styles/MainContent.module.css';

// Define the props interface
interface MainContentProps {
  sidebarIsOpen: boolean; // Indicates if the sidebar is open
}

const MainContent: React.FC<MainContentProps> = ({ sidebarIsOpen }) => (
  <div
    className={`${styles.mainContent} ${
      sidebarIsOpen ? styles.sidebarOpen : styles.sidebarHidden
    }`}
  >
    <section id="about">
      <AnimatedSection />
    </section>
    <section id="skills">
      <SkillsSection />
    </section>
    <section id="experience">
      <ExperienceSection />
    </section>
    <section id="education">
      <EducationSection />
    </section>
    <section id="certifications">
      <CertificationsSection />
    </section>
    <section id="languages">
      <LanguagesSection />
    </section>
  </div>
);

export default MainContent;
