import React, { KeyboardEvent } from 'react';
import classNames from 'classnames';
import {
  FaEnvelope,
  FaGithub,
  FaLinkedin,
  FaBars,
  FaTimes,
} from 'react-icons/fa';
import styles from '../styles/Sidebar.module.css';
import profileImage from '../assets/profile.jpg';

interface NavItem {
  id: string;
  label: string;
}

interface SidebarProps {
  isOpen: boolean; // Whether the sidebar is open
  isMobile: boolean; // Whether the device is mobile
  onToggleSidebar: () => void; // Function to toggle sidebar visibility
  onCloseSidebar: () => void; // Function to close the sidebar
}

const Sidebar: React.FC<SidebarProps> = ({
  isOpen,
  isMobile,
  onToggleSidebar,
  onCloseSidebar,
}) => {
  const navItems: NavItem[] = [
    { id: 'about', label: 'About' },
    { id: 'skills', label: 'Skills' },
    { id: 'experience', label: 'Experience' },
    { id: 'education', label: 'Education' },
    { id: 'projects', label: 'Projects' },
  ];

  const handleHamburgerKeyDown = (event: KeyboardEvent<HTMLDivElement>) => {
    if (event.key === 'Enter' || event.key === ' ') {
      onToggleSidebar();
    }
  };

  const scrollToSection = (id: string) => {
    const section = document.getElementById(id);
    if (section) {
      section.scrollIntoView({ behavior: 'smooth' });
    }
    if (isMobile) {
      onCloseSidebar();
    }
  };

  const handleNavClick = (id: string) => {
    scrollToSection(id);
  };

  const renderNavItems = () =>
    navItems.map((item) => (
      <li key={item.id}>
        <button
          type="button"
          onClick={() => handleNavClick(item.id)}
          className={styles.navLink}
        >
          {item.label}
        </button>
      </li>
    ));

  return (
    <>
      {/* Hamburger Menu for Mobile */}
      <div
        className={styles.hamburger}
        onClick={onToggleSidebar}
        role="button"
        tabIndex={0}
        onKeyDown={handleHamburgerKeyDown}
        aria-label="Toggle Sidebar"
      >
        {isOpen ? <FaTimes /> : <FaBars />}
      </div>

      {/* Sidebar */}
      <aside
        className={classNames(styles.sidebar, {
          [styles.open]: isOpen,
        })}
      >
        {/* Profile Image */}
        <img
          src={profileImage}
          alt="Karim Safiullin"
          className={styles.profileImage}
        />
        <h1>Karim Safiullin</h1>
        <p className={styles.jobTitle}>Senior Data Engineer</p>

        {/* Contact Icons */}
        <div className={styles.contactIcons}>
          <a
            href="mailto:karim.safiullin.career@gmail.com"
            target="_blank"
            rel="noopener noreferrer"
            className={styles.iconLink}
            aria-label="Email"
          >
            <FaEnvelope />
          </a>
          <a
            href="https://github.com/BondaiKa"
            target="_blank"
            rel="noopener noreferrer"
            className={styles.iconLink}
            aria-label="GitHub"
          >
            <FaGithub />
          </a>
          <a
            href="https://linkedin.com/in/karim-safiullin"
            target="_blank"
            rel="noopener noreferrer"
            className={styles.iconLink}
            aria-label="LinkedIn"
          >
            <FaLinkedin />
          </a>
        </div>

        {/* Navigation Links */}
        <nav>
          <ul className={styles.navLinks}>{renderNavItems()}</ul>
        </nav>

        {/* Footer */}
        <footer className={styles.footer}>
          <p>© 2024 Karim Safiullin</p>
        </footer>
      </aside>
    </>
  );
};

export default Sidebar;
